<template>
  <component
    :is="tag"
    v-bind="$attrs"
    class="button"
    :class="{
      [`button_${type}`]: true,
      [`button_size-${size}`]: size,
      button_loading: loading,
      button_disabled: disabled
    }"
    v-tooltip="tooltip"
  >
    <loader v-if="loading" size="15px" border="3px" absolute />
    <slot></slot>
  </component>
</template>

<script>
import Loader from '@/components/Loader';

export default {
  props: {
    type: String,
    loading: Boolean,
    disabled: Boolean,
    size: String,
    tag: {
      type: String,
      default: 'button',
    },
  },
  components: { Loader },
  data() {
    return {
      tooltip: null,
      tooltipTimeout: null,
    };
  },
  methods: {
    showTooltip({ content, duration = 5000 }) {
      clearTimeout(this.tooltipTimeout);

      this.tooltip = {
        content,
        trigger: 'manual',
        show: true,
      };

      this.tooltipTimeout = setTimeout(() => {
        this.tooltip = null;
      }, duration);
    },
  },
};
</script>

