  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  color: var(--text-primary);
  cursor: pointer;
  word-break: break-word;
  text-align: center;

  &_size {
    &-lg {
      font-size: 16px;
      padding: 8px 16px;
    }
  }

  &_primary {
    background-color: $primary;
    color: #fff;
  }

  &_negative {
    background-color: #ff4d4d;
    border-color: #ff4d4d;
    color: #fff;
  }

  &_loading {
    pointer-events: none;
  }

  &_disabled {
    pointer-events: none;
